import gql from 'graphql-tag'

import { FGeoPointFragmentDoc } from '../graph/generated'

export const AddressSuggestQuery = gql`
  query GAddressSuggest($search: String!) {
    suggest: suggestGeolocation(address: $search) {
      state
      city
      street
      houseNumber
      gps {
        ...FGeoPoint
      }
    }
  }
  ${FGeoPointFragmentDoc}
`
