import React from 'react'

import { useGoogleMap } from '../../../atoms/BaseMap'
import {
  FOrderMapDeliveryFragment,
  QOrderMapDeliveriesQuery,
  SOrderMapDeliveriesDevice,
  useQOrderMapDeliveries,
  useSOrderMapDeliveries,
} from '../../../graph/generated'
import { gpsToLatLng } from '../../../helpers/gps'
import { useOnConnectionRestored } from '../../../hooks/useOnConnectionRestored'
import { OrderMapDelivery } from '../atoms/OrderMapDelivery'

interface IProps {
  branches: ReadonlyArray<ID>
}

export const OrderMapDeliveries: React.FC<IProps> = ({ branches }) => {
  const { data, refetch, updateQuery } = useQOrderMapDeliveries(
    { branches },
    {
      skip: branches.length === 0,
    },
  )

  useOnConnectionRestored(refetch)

  useSOrderMapDeliveries(
    { branches },
    {
      skip: branches.length === 0,
      onSubscriptionData({ subscriptionData: { data: dataUpdate } }) {
        if (!(dataUpdate && dataUpdate.update.device)) {
          return
        }
        updateQuery(prev =>
          handleSubscriptionUpdate(dataUpdate.update.device!, prev),
        )
      },
    },
  )

  const map = useGoogleMap()

  if (!data || !data.deliveries || data.deliveries.length === 0) {
    return null
  }

  const mapDeliveries = data.deliveries
    .filter(driver => Boolean(driver.device && driver.device.lastPosition))
    .map(driver => (
      <OrderMapDelivery
        key={driver.id}
        position={driver.device!.lastPosition!.gps}
        label={driver.initials || driver.id}
        isEmpty={driver.countUndeliveredOrders === 0}
        onClick={() => {
          if (map) {
            map.panTo(gpsToLatLng(driver.device!.lastPosition!.gps))
          }
        }}
      />
    ))

  return <>{mapDeliveries}</>
}

function handleSubscriptionUpdate(
  device: SOrderMapDeliveriesDevice,
  prev: QOrderMapDeliveriesQuery,
) {
  let driverUpdated = false
  // map through current driver and try to find the updated one
  const deliveries = prev.deliveries.map(driver => {
    if (driver.device && driver.device.id === device.id) {
      if (device.isFinalized) {
        // device is no longer used and will be reassigned
        return null
      }
      if (driver.id === device.user.id) {
        driverUpdated = true
        return {
          ...driver,
          device,
        }
      } else {
        // driver got assigned different device
        // remove it from here to add it later
        return null
      }
    }
    return driver
  })
  if (!driverUpdated) {
    const {
      user: { __typename, id, initials, countUndeliveredOrders },
      ...userDevice
    } = device
    deliveries.push({
      __typename,
      id,
      initials,
      countUndeliveredOrders,
      device: userDevice,
    })
  }
  return {
    ...prev,
    deliveries: deliveries.filter(Boolean) as FOrderMapDeliveryFragment[],
  }
}
